<template>
  <div class="back-top-demo">
    <div class="back-top-demo__item">
      <h2 id="back-top-title">
        <a href="#back-top-title" aria-hidden="true" class="header-anchor"></a>
        BackTop 
      </h2>
      <h3 id="gai-shu">
        <a href="#gai-shu" aria-hidden="true" class="header-anchor"></a>
        
      </h3>
      <p></p>
    </div>
    <div class="back-top-demo__item">
      <h3 id="dai-ma-shi-li">
        <a href="#dai-ma-shi-li" aria-hidden="true" class="header-anchor"></a>
        
      </h3>
      <demo>
        <template slot="source">
          <span class="demonstration"></span>
          <vm-back-top></vm-back-top>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeA"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
          <p></p>
          <p> 30px 400px </p>
        </template>
      </demo>
      <demo>
        <template slot="source">
            <span class="demonstration"></span>
            <vm-back-top :height="100" :bottom="200" :timing="'ease'">
              <div class="top"></div>
            </vm-back-top>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeB"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
          <p></p>
          <p> 200px, 100px </p>
          <p>ease</p>
        </template>
      </demo>
      <demo>
        <template slot="source">
          <span class="demonstration"></span>
          <vm-back-top :height="100" :bottom="300" :duration="500" :options="options" @navClick="handleNavClick">
          </vm-back-top>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeC"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
          <p></p>
          <p></p>
          <p></p>
        </template>
      </demo>
    </div>
    <div class="rate-demo__item">
      <h3 id="attributes">
        <a href="./#attributes" aria-hidden="true" class="header-anchor"></a>
        Attributes
      </h3>
      <table class="table">
        <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        </thead>
        <tbody>
        <tr>
          <td>height</i></td>
          <td>BackTop</td>
          <td>number</td>
          <td></td>
          <td>400</td>
        </tr>
        <tr>
          <td>bottom</i></td>
          <td></td>
          <td>number</td>
          <td></td>
          <td>30</td>
        </tr>
        <tr>
          <td>right</i></td>
          <td></td>
          <td>number</td>
          <td></td>
          <td>30</td>
        </tr>
        <tr>
          <td>duration</i></td>
          <td> </td>
          <td>number</td>
          <td></td>
          <td>1000</td>
        </tr>
        <tr>
          <td>timing</i></td>
          <td></td>
          <td>string</td>
          <td>linear/ease</td>
          <td>linear</td>
        </tr>
        <tr>
          <td>options</i></td>
          <td>labelid</td>
          <td>array</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>navItemClass</i></td>
          <td></td>
          <td>string</td>
          <td></td>
          <td></td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="rate-demo__item">
      <h3 id="event">
        <a href="./#event" aria-hidden="true" class="header-anchor"></a>
        Events
      </h3> 
      <table class="table">
        <thead>
        <th></th>
        <th></th>
        <th></th>
        </thead>
        <tbody>
        <tr>
          <td>click</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>navClick</td>
          <td></td>
          <td>id</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
    import { sourcecodeA, sourcecodeB, sourcecodeC } from './template.js'
  import Demo from './Demo.vue'

  export default {
    data () {
      return {
          options:[
              {
                  id:'back-top-title',
                  label:'BackTop'
              },
              {
                  id:'gai-shu',
                  label:''
              },
              {
                  id:'dai-ma-shi-li',
                  label:''
              }
          ],
        sourcecodeA,
        sourcecodeB,
        sourcecodeC

      }
    },
    methods: {
        handleNavClick (val) {
            console.log(222)
        console.log(val);
      }
    },
    components: {
      Demo
    }
  }
</script>
<style lang="less" rel="stylesheet/less">
  .top{
    padding: 10px;
    background: rgba(0, 153, 229, .7);
    color: #fff;
    text-align: center;
    border-radius: 2px;
  }
  .back-top-demo {
    h2, h3 {
      font-weight: 400;
      color: #1f2f3d;
    }
    h2 {
      font-size: 28px;
      margin: 0
    }
    h3 {
      font-size: 22px;
      margin: 45px 0 15px;
    }
    h3, h2 {
      &:hover {
        a {
          opacity: .4;
        }
      }
      a {
        float: left;
        margin-left: -20px;
        opacity: 0;
        cursor: pointer;
        color: #4078c0;
        text-decoration: none;
        &:hover {
          opacity: .4;
        }
      }
    }
    p {
      font-size: 14px;
      color: #5e6d82;
      line-height: 1.5em;
      a {
        color: #2d8cf0;
        background: 0 0;
        text-decoration: none;
        outline: 0;
        cursor: pointer;
        transition: color .2s ease;
        &:hover {
          color: #57a3f3;
        }
      }
    }
    .new {
      font-style: normal;
      color: #FF4949;
      code {
        background-color: #FF4949 !important;
        color: #fff !important;
      }
    }
    .table {
      border-collapse: collapse;
      width: 100%;
      background-color: #fff;
      color: #5e6d82;
      font-size: 14px;
      margin-bottom: 45px;
      line-height: 1.5em;
      th {
        text-align: left;
        border-top: 1px solid #eaeefb;
        background-color: #eff2f7;
        white-space: nowrap;
      }
      th, td {
        border-bottom: 1px solid #eaeefb;
        padding: 10px;
        max-width: 250px;
      }
    }
  }
</style>



